<template>
  <div class="page-renew">

    <mt-header title="">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>

    <div class="box check-info">
      <div class="check-date">
        <div class="check-in"> <span>701</span></div>
        <div class="check-day">29</div>
        <div class="check-out"> <span>729</span></div>
      </div>
    </div>

    <div class="fix-bottom">
      <mt-button @click.native="handleRenew" size="large" type="primary"></mt-button>
    </div>
    
  </div>
</template>

<script>
import { MessageBox } from 'mint-ui';

export default {
  name: 'page-renew',

  components: {
  },

  methods: {
    handleRenew: function() {

      MessageBox({
        title: '',
        message: '<span>710<\/span><span>714<\/span>',
        type:'confirm',
        showCancelButton: true,
        confirmButtonText: '',
        cancelButtonText: '',
      }).then(action => {
        if(action == "confirm"){
          window.history.back();
        }
      });

    }
  }
};
</script>



